.ce-left-links {
  font-family: "Century Gothic", CenturyGothic, AppleGothic, sans-serif;
  line-height: 200%;
  text-transform: uppercase;
  padding-right: 2em; }

.ce-left-links a,
.ce-left-links a:visited {
  color: #73af55; }

.ce-left-links .current-page-link a,
.ce-left-links .current-page-link a:visited {
  color: orange;
  font-weight: bolder; }

.ce-left-links ul ul {
  padding-left: 1em; }

.ce-left-links > ul {
  list-style-type: none;
  margin-left: 0;
  padding-left: 0; }
