.modal-overlay {
  box-sizing: border-box;
  background-color: rgba(0, 0, 0, 0.7);
  height: 100%;
  bottom: 0;
  left: 0;
  right: 0;
  top: 0;
  display: flex;
  position: fixed;
  width: 100%;
  z-index: 1; }

.modal-box {
  box-shadow: 0 0 60px 10px rgba(0, 0, 0, 0.9);
  box-sizing: border-box;
  min-width: 200px;
  max-width: 70%;
  max-height: 75vh;
  margin: auto;
  position: relative; }
  .modal-box .modal-content {
    box-sizing: border-box;
    background-color: white;
    overflow: auto;
    padding: 3em;
    width: 100%;
    height: 100%; }
  .modal-box .close-icon {
    box-sizing: border-box;
    cursor: pointer;
    display: block;
    height: 40px;
    position: absolute;
    top: 20px;
    right: 20px;
    text-align: center;
    width: 40px; }
    .modal-box .close-icon:hover {
      color: red; }
