html {
  height: 100%;
  font-family: "Century Gothic", CenturyGothic, AppleGothic, sans-serif; }

body {
  background-color: white;
  color: #222222;
  height: 100%;
  padding: 0px;
  margin: 0px; }

input[type='radio'] {
  box-shadow: none; }

#root {
  height: 100%; }

#top-react-div {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  margin: auto;
  position: relative; }

#main-menu-bar {
  text-transform: uppercase; }
  #main-menu-bar li a,
  #main-menu-bar li a:visited {
    color: black;
    text-align: center;
    text-decoration: none; }
  #main-menu-bar li.current-page-link a,
  #main-menu-bar li.current-page-link a:visited {
    color: orange; }
  #main-menu-bar li a:hover {
    text-decoration: underline; }
  #main-menu-bar li:last-child {
    color: #669e4a; }
  #main-menu-bar li:last-child a,
  #main-menu-bar li:last-child a:visited {
    color: #669e4a; }

a {
  text-decoration: none; }

a:hover {
  cursor: pointer;
  text-decoration: underline; }

.page-with-padding {
  padding: 1em; }

#leftcontent {
  width: 80%;
  min-width: 800px;
  max-width: 1000px;
  margin: auto;
  font-family: Calibri, "Segoe UI", Candara, Segoe, Optima, Arial, sans-serif;
  color: #3f3f3f; }
  #leftcontent h1 {
    display: inline-block;
    padding-bottom: 5px;
    padding-right: 100px;
    margin-left: 1em;
    margin-bottom: 2em;
    border-bottom: 1px solid #73af55;
    font-family: Calibri, "Segoe UI", Candara, Segoe, Optima, Arial, sans-serif;
    font-size: 80%;
    text-transform: uppercase;
    color: #92d050;
    font-weight: normal; }
    #leftcontent h1 span {
      font-size: 150%;
      color: #73af55; }
  #leftcontent p,
  #leftcontent ol,
  #leftcontent ul {
    margin-left: auto;
    margin-right: auto;
    width: 800px; }
  #leftcontent ol,
  #leftcontent ul {
    padding-left: 4em; }

.fancy-title {
  color: #9dc3e6;
  text-transform: uppercase; }
  .fancy-title::first-letter {
    font-family: Brush Script MT, cursive;
    font-size: 220%; }

.float-left-image {
  float: left;
  margin-right: 0.5em;
  width: 100px; }

/* Contain floats. */
.cf:before,
.cf:after {
  content: '\0020';
  display: block;
  overflow: hidden;
  visibility: hidden;
  width: 0;
  height: 0; }

.cf:after {
  clear: both; }

/* Contain floats *and margins*. */
.cfm:before,
.cfm:after {
  content: ' ';
  display: table; }

.cfm:after {
  clear: both; }

/*General  */
.clear {
  clear: both; }

/*************** Font Family ************/
.gill {
  font-family: 'gill sans mt', arial, sans-serif; }

.candara {
  font-family: 'candara', 'trebuchet ms', helvetica, sans-serif; }

.verdana {
  font-family: verdana, geneva, sans-serif; }

.clear {
  clear: both; }

.italic {
  font-style: italic; }

.center {
  text-align: center; }

.topofview {
  position: fixed;
  top: 1em; }

.thin {
  font-weight: lighter; }

.maroon {
  color: maroon; }

.bold {
  font-weight: bolder; }

.black {
  color: black; }

.white {
  color: white; }

.gray {
  color: #292929; }
