.registration-landing {
  display: flex;
  flex-grow: 2;
  position: relative; }
  .registration-landing .button-div {
    position: absolute;
    background-color: transparent;
    color: inherit;
    display: block;
    margin: 0 auto;
    left: 50%;
    transform: translate(-50%);
    bottom: 20%; }
  .registration-landing button {
    background-color: transparent;
    border: 1px solid;
    color: inherit;
    display: block;
    padding: 1.5em;
    margin: 0 auto; }

.left-side {
  background-size: 100%;
  color: white;
  flex-basis: 0;
  flex-grow: 1;
  position: relative; }
  .left-side img {
    overflow-y: hidden;
    position: absolute;
    width: 100%;
    z-index: 0; }

.right-side {
  position: relative;
  flex-basis: 0;
  flex-grow: 1; }

div.sentence {
  position: absolute;
  font-size: 300%;
  margin: 20vh 0 2em 10%;
  z-index: 2; }
  div.sentence span {
    display: block; }
  div.sentence span.first-part {
    font-weight: bold;
    text-transform: uppercase; }
  div.sentence span.rest-of-sentence {
    font-family: "Century Gothic", CenturyGothic, AppleGothic, sans-serif; }
