.registration-page {
  box-shadow: 6px 6px 6px 0 #c2c2c2;
  border: 1px solid gray;
  font-family: "Century Gothic", CenturyGothic, AppleGothic, sans-serif;
  margin: 1em auto;
  padding: 0.2em 1em 2em 1em;
  width: 600px; }
  .registration-page h1 {
    font-family: "Adobe Garamond Pro", Garamond, Baskerville, "Baskerville Old Face", "Hoefler Text", "Times New Roman", serif;
    font-weight: normal;
    font-size: 48px;
    color: orange;
    margin-bottom: 0px;
    margin-top: 16px; }
  .registration-page h2 {
    font-weight: normal;
    font-family: "Adobe Garamond Pro", Garamond, Baskerville, "Baskerville Old Face", "Hoefler Text", "Times New Roman", serif;
    font-size: 24px;
    color: orange;
    margin-top: 0px; }
  .registration-page h1.vbs-header,
  .registration-page h2.vbs-header {
    color: #73af55; }
  .registration-page h3 {
    color: #5f5f5f; }
  .registration-page p {
    color: #5f5f5f; }
  .registration-page input[type='checkbox'] {
    color: red;
    box-shadow: 0 0 0 0; }
  .registration-page .error-list {
    color: #5f5f5f; }
  .registration-page .error-list ul {
    margin-top: 0.2em; }
  .registration-page .error-list i {
    color: orange; }
  .registration-page .success {
    background-color: green; }
  .registration-page .failure {
    background-color: red; }
  .registration-page label {
    color: #5f5f5f;
    font-family: "Century Gothic", CenturyGothic, AppleGothic, sans-serif;
    font-weight: bold;
    font-size: 12px;
    margin-bottom: 5px; }
  .registration-page .checklist-pattern > label {
    color: #73af55; }
  .registration-page .registration-checkbox > label {
    color: #73af55; }
  .registration-page .registration-modal ul {
    list-style-type: none;
    padding: 0; }
    .registration-page .registration-modal ul li {
      line-height: 1.5; }
    .registration-page .registration-modal ul .check-list-summary li {
      padding: 0 15px; }
  .registration-page .registration-modal button {
    margin-right: 1em; }
  .registration-page .registration-disclaimer {
    font-family: Calibri, "Segoe UI", Candara, Segoe, Optima, Arial, sans-serif; }
